<template>
  <div class="content">
    <!-- 导航头 -->
    <div class="top">
      <!-- <van-nav-bar
        title="商城用户注册"
        left-arrow
        @click-left="onClickLeft"
      /> -->
      <div class="sign">
        <div>注册智家商城</div>
      </div>
    </div>
    <!-- 表单信息 -->
    <div class="info">
      <div style="padding: 0.2rem 0.32rem 0.2rem 0;background:#fff;position: relative;height:0.56rem;">
        <input type="tel" placeholder="请输入手机号" class="info-mm" v-model="mobileAccount" required maxlength=13 @keyup="keyup">
        <img class="editable-clear-x" @click="clear" :src='`${$cdnImageUrl}/login/clear.png`'>
        <div class="line"></div>
      </div>
      <div style="padding: 0.2rem 0.32rem 0.2rem 0;background:#fff;position: relative;height:0.56rem;" v-show="captchaShow">
          <input type="text" v-model="captcha" placeholder="请输入图形验证码" class="info-mm" maxlength=6>
          <img class="verify-two" :src="src" alt="" @click="getPhoto">
          <div class="line"></div>
        </div>
      <div style="padding: 0.2rem 0.32rem 0.2rem 0;background:#fff;position: relative;height:0.56rem;">
        <input type="text" v-model="messageMM" placeholder="请输入验证码" class="info-mm" @blur="safetyCount" maxlength=6>
        <div class="show" v-show="!messageMMType" :style="{color:('#ccc')}">{{title}}</div>
        <div class="show" v-show="messageMMType" @click="validateBtn" :style="{color:(allow?'#2979FF':'#333')}">{{title}}</div>
        <div class="line"></div>
      </div>
      <div class="operation">
        <button class="operation-btn" :class="disabled?'btn-disabled':''" @click="login" :disabled="disabled">注册</button>
      </div>
    </div>
     <!-- 注册遮罩 -->
    <van-popup
      v-model="show"
      position="bottom"
      :style="{ height: 'calc(100vh - 3.36rem)' }"
    >
      <register @agree="agree"></register>
    </van-popup>
  </div>
</template>
<script>
import personalApi from '@/http/service/login.js';
import register from './register.vue'
export default {
  components: {
    register
  },
  data() {
    return {
      mobileAccount: '', // 手机账号
      messageMM: '', // 验证码密码
      show: true,
      title: '获取验证码',
      allow: true,
      captcha: '', // 图片验证码
      src: '',
      captchaShow: false,
      disabled: true,
      phoneReg: /^1[3456789]\d{9}$/,
      messageMMType: false, // 控制短信验证码显隐
    }
  },
  created() {
    this.$util.smMsgSdk()
  },
  watch: {
    messageMM(val, old) {
      if (val.length !== 6) {
        this.disabled = true
      } else {
        this.disabled = false
      }
    },
    // mobileAccount (newValue, oldValue) {
    //   if (newValue.length > oldValue.length) {
    //     this.mobileAccount = newValue.replace(/\s/g, '').replace(/(\d{3})(\d{0,4})(\d{0,4})/, '$1 $2 $3')
    //   } else {
    //     this.mobileAccount = this.mobileAccount.trim()
    //   }
    //   if (newValue.length === 13) {
    //     this.messageMMType = true
    //   } else {
    //     this.messageMMType = false
    //   }
    // },
  },
  methods: {
    /**
   * @description  华为输入法光标错位替代
   */
    keyup() {
      let phone = this.mobileAccount
      phone = phone.replace(/[^0-9]/g, '')
      // 分组成344，并且加上' '
      var temp = []
      temp[0] = phone.slice(0, 3)
      temp[1] = phone.slice(3, 7)
      temp[2] = phone.slice(7)
      if (temp[2].length > 0) {
        temp[1] += ' '
        temp[0] += ' '
      } else if (temp[1].length > 0) {
        temp[0] += ' '
      }
      if (this.mobileAccount.length === 13) {
        this.messageMMType = true
        this.verification()
      } else {
        this.messageMMType = false
      }
      this.mobileAccount = temp.join('')
    },
    /**
   * @description  验证码获取
   */
    validateBtn() {
      const dealZh = this.mobileAccount.replace(/\s/g, '')
      this.$util.dealSmDeviceId().then(deviceId => {
        let data = {
          mobile: dealZh
        }
        if (this.captchaShow) {
          data.imgCaptcha = this.captcha
        }
        // 手机号验证
        if (!(/^1[3456789]\d{9}$/.test(dealZh))) {
          // this.$toast('手机号格式不正确，请重新输入')
          return
        }
        // 图片验证校验
        if (this.captchaShow && this.captcha === '') {
          this.$toast('请输入图片验证码')
          return
        }
        if (this.allow) {
          personalApi.register(data).then(res => {
            gio('track', 'SignUpSendSmsBtn')
            if (res.data.success === false) {
              this.$toast(res.data.message)
            } else {
              this.$toast('短信验证码发送成功，请及时验证！')
              // 倒计时
              let time = 60;
              let timer = setInterval(() => {
                if (time === 0) {
                  this.allow = true
                  clearInterval(timer);
                  this.title = '重发验证码';
                } else {
                  this.title = '重新发送(' + time + 's)';
                  this.allow = false;
                  time--
                }
              }, 1000)
            }
          })
        }
      })
    },
    /**
   * @description  注册快速登录
   */
    login() {
      this.messageMM = this.messageMM.replace(/\s/g, '')
      const dealZh = this.mobileAccount.replace(/\s/g, '')
      this.$util.dealSmDeviceId().then(deviceId => {
        let data = {
          mobile: dealZh,
          captcha: this.messageMM,
          regSource: 'MSmsregister'
        }

        this.$store.dispatch('doRegister', data).then(res => {
          if (res.code === 400) {
            this.$toast(res.message)
          } else {
            gio('track', 'MSignUpSuccess')
            sessionStorage.setItem('isRecordShareRelation', '') // 登录后复购分佣接口需重新调用
            this.$router.push('/registerSuccess')
          }
        })
        // personalApi.registerAccount(data).then(res => {
        //   if (res.data.success) {
        //     this.storeInfo(res.data)
        //     gio('track', 'MSignUpSuccess')
        //     this.$router.push('/registerSuccess')
        //   } else {
        //     this.$toast(res.data.message)
        //   }
        // })
      })
    },
    /**
   * @description  同意条款
   */
    agree(res) {
      this.show = res
    },
    /**
   * @description  用户信息存储
   */
    // storeInfo(data) {
    //   let result = data.data
    //   let mid = result.mid
    //   let ucid = result.ucId
    //   let accessToken = result.accessToken
    //   let sessionKey = result.sessionKey
    //   let sessionValue = result.sessionValue
    //   let token = result.token
    //   let userInfo = {}
    //   userInfo.username = result.userName
    //   userInfo.avatarImage = result.avatarImageFileId
    //   userInfo.loginName = result.loginName
    //   userInfo.birthday = result.birthday
    //   userInfo.phone = result.mobile
    //   userInfo.email = result.email
    //   userInfo.cartNumber = result.cartNumber
    //   userInfo.gender = result.gender
    //   userInfo.isNewUser = result.isNewUser
    //   userInfo.isStoreMember = result.isStoreMember
    //   userInfo.nickName = result.nickName
    //   userInfo.rankName = result.rankName
    //   userInfo.promotionCode = result.promotionCode
    //   userInfo.resultSign = result.resultSign

    //   this.$store.commit('setLoginStatus', true, { root: true });
    //   this.$store.commit('setmId', mid, { root: true });
    //   this.$store.commit('setUcId', ucid, { root: true });
    //   this.$store.commit('setAcsToken', accessToken, { root: true })
    //   this.$store.commit('setSessionKey', sessionKey, { root: true })
    //   this.$store.commit('setSessionValue', sessionValue, { root: true })
    //   this.$store.commit('setUserInfo', userInfo, { root: true })
    //   this.$store.commit('setToken', token, { root: true });

    //   // set locaStorage
    //   localStorage.setItem('isLogin', true)
    //   localStorage.setItem('ucId', ucid)
    //   localStorage.setItem('token', token)
    //   // localStorage.setItem('sdToken', sdToken)
    //   localStorage.setItem('accessToken', accessToken)
    //   localStorage.setItem('mId', mid)
    //   localStorage.setItem('sessionKey', sessionKey)
    //   localStorage.setItem('sessionValue', sessionValue)
    //   localStorage.setItem('userInfo', JSON.stringify(userInfo))
    //   this.$store.dispatch('account/isHasStore', { mid, accessToken })
    // },

    /**
    * @description  图片验证
   */
    verification() {
      const dealZh = this.mobileAccount.replace(/\s/g, '')
      if (!(this.phoneReg.test(dealZh))) {
        this.$toast('手机号格式不正确，请重新输入')
        return
      }
      gio('track', 'MSignUpMobileInput', {
        signupmobile: dealZh
      })
      let data = {
        mobile: dealZh
      }
      personalApi.needImg(data).then(res => {
        if (res.data.data) {
          this.captchaShow = true
          this.getPhoto()
        } else {
          this.captchaShow = false
        }
      })
    },
    /**
   * @description  获取图片验证码
   */
    getPhoto() {
      this.src = this.$HOST + '/v3/platform/web/member/register/getImgCaptcha.html?rnd=' + Math.random() + '&flag=' + localStorage.getItem('sessionValue')
    },
    /**
   * @description  账号清除
   */
    clear() {
      this.mobileAccount = ''
    },
    /**
   * @description  验证码输入埋点
   */
    safetyCount() {
      gio('track', 'MSignUpSmsInput', {
        signupmobilecode: this.messageMM
      })
    },
  },
}
</script>
